export default {
	data() {
		return {
			isMan: true,
			tagsList: [],
			currindex: -1,
			selectedList: []
		}
	},
	onLoad(options) {
		console.log(options)
		if(options.sex == 1) {
			this.isMan = true
		}else{
			this.isMan = false
		}
	},
	created() {
		this.$http.get({
			url: '/v1/rest/public/findSysLabel',
			data:{}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.tagsList = resp.data
			}
		})
	},
	methods: {
		skip() {
			uni.navigateBack()
		},
		handleSelect(item,index) {
			this.currindex = index
			let selectedIndex = this.selectedList.indexOf(item.value)
			 if(selectedIndex >= 0) {
				this.selectedList.splice(selectedIndex, 1)
			}else{
				this.selectedList.push(item.value)
			}
		},
		handleSure() {
			this.$http.get({
				url: '/v1/rest/login/registerLabelSaveOne',
				data: {
					userId: uni.getStorageSync('userInfo').userId,
					labelId: this.selectedList.join(',')
				},
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					uni.navigateBack()
				}
			})
		}
	}
}
